import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS, Div, Caption } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderBack from '@vkontakte/vkui/dist/components/PanelHeaderBack/PanelHeaderBack';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Information from '../img/staytuned-min.png';

const osName = platform();

const Lessons = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderBack onClick={props.go} data-to="useful">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderBack>}>
			Расписание
		</PanelHeader>
        <Caption weight= "semibold" style={{ paddingTop: 20, paddingBottom: 20, textAlign: 'center' }}>КАНИКУЛЫ</Caption>
        <Div style={{textAlign: 'center'}} >Очень скоро здесь появится расписание ЕГЭ 2020</Div>
		<img className="Persik" src={Information} alt="Persik The Cat"/>
	</Panel>
);

Lessons.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Lessons;